@import url('https://fonts.googleapis.com/css2?family=Fraunces:wght@700&family=Nunito+Sans:wght@300&display=swap');


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    min-height: 100vh;
    /* font-family: 'Fraunces', serif; */
}

.container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column; 
}

.title{
    font-size: 3rem;
    margin: 6vh;
    border: 7px dashed rgb(1, 17, 160);
    border-radius: 20px;
    padding: 1vh 5vw;
}

.cards{
    display: flex;
    justify-content: center;
    /* align-items: center; */
    flex-wrap: wrap;
    max-width: 1300px;
    margin: 0 auto;
}
.card{
    width: 400px;
    height: max-content;
    margin: 1rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    align-items: center;
    box-shadow: rgb(0, 0, 0, 0.24) 0px 3px 8px;
}

.image{
    width: 380px;
    aspect-ratio: 1/1;
    object-fit: cover;
}

.tour-info{
    margin: 20px 5px;
}
.tour-price{
    font-size: 1.3rem;
    color: #1faa59;
}
.tour-name{
    font-size: 1.5rem;
}
.read-more{
    color: #62b7d6;
    cursor: pointer;
}
.btn-red{
    margin-top: 18px;
    padding: 10px 80px;
    border: 1px solid #b4161d;
    cursor: pointer;
    border-radius: 10px;
    font-size: 18px;
    font-weight: bold;
    background-color: #b4161b21;
}
.btn-red:hover{
    background-color: rgb(242, 84, 84);
    color: white;
    transition: all 0.2s;
}
.refresh{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.button-white{
    margin-top: 18px;
    padding: 10px 80px;
    border: 1px solid white;
    cursor: pointer;
}
.button-white:hover{
    background-color: white;
    color: black;
    transition: all 0.2s;
}